// Swiper customize
.swiper-button-next,
.swiper-button-prev {
    --swiper-navigation-size: 24px;
    --swiper-navigation-top-offset: 50%;
    --swiper-navigation-sides-offset: 10px;
    --swiper-navigation-color: rgba(255, 255, 255, 0.2);
    display: flex;
    width: 56px;
    height: 56px;
    padding: 10px;
    font-size: 24px;
    --swiper-navigation-color: var(--tc-neutral-900);
    border-radius: 30px;
    border: none;
    &:hover,
    :active {
        border-color: var(--tc-theme-primary);
        background-color: var(--tc-theme-primary) !important;
        --swiper-navigation-color: var(--tc-system-white);
    }
}
.swiper-button-next::after,
.swiper-button-prev::after {
    content: '';
}
.swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    text-align: center;
    line-height: 10px;
    font-size: 12px;
    color: #000;
    opacity: 1;
    background: rgba(0, 0, 0, 0.2);
}
.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
