blockquote {
    color: #ffffff;
    text-align: center;
    font-size: 31px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%; /* 37.2px */
}
ul.timeline {
    list-style-type: none;
    position: relative;
    &::before {
        content: ' ';
        background: var(--tc-neutral-300);
        display: inline-block;
        position: absolute;
        left: 5px;
        width: 1px;
        height: 100%;
        z-index: 400;
    }
    .timeline-item {
        padding-left: 20px;
        &::before {
            content: ' ';
            background: var(--tc-system-white);
            display: inline-block;
            position: absolute;
            border-radius: 50%;
            border: 3px solid var(--tc-theme-primary);
            left: -5px;
            width: 20px;
            height: 20px;
            z-index: 400;
        }
        li {
            list-style-type: initial;
        }
    }
}
svg {
    .fill-dark {
        fill: var(--tc-system-dark);
    }
    .fill-white {
        fill: var(--tc-system-white);
    }
    .fill-primary {
        fill: var(--tc-theme-primary);
    }
    .fill-neutral-100 {
        fill: var(--tc-neutral-100);
    }
    .fill-primary-soft {
        fill: var(--tc-theme-primary-soft);
    }
    .stroke-dark {
        stroke: var(--tc-system-dark);
    }
    .stroke-white {
        stroke: var(--tc-system-white);
    }
}
.table-white {
    --bs-table-bg: var(--tc-system-white);
}
