  height: 15px;
  width: 15px;
  background-color: var(--tc-theme-primary);
  border-radius: 50%;
  border: 4px solid var(--tc-system-white);
  box-shadow: 0 13px 35px -12px rgba(35, 35, 35, 0.1);
  transition: 0.2s ease-in-out;
}

